.card-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  color: white;
  gap: 0;
  padding: 0;
  height: 100%;
  box-sizing: border-box;
}

/* Í≥µÌÜµ Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.card1, .card2, .card3, .card4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25%; /* Í∏∞Î≥∏: 4Í∞ú Í∞ÄÎ°ú ÎÇòÏó¥ (PC ÌôòÍ≤Ω) */
  padding: 2rem;
  text-align: center;
  transition: transform 0.3s ease;
  color: white;
  border-radius: 0;
  box-sizing: border-box;
}

/* Ïπ¥Îìú ÏÉâÏÉÅ ÏßÄÏ†ï */
.card1 { background-color: #5a91d4; } /* Í≥µÏßÄÏÇ¨Ìï≠ */
.card2 { background-color: #9bbbd4; } /* Ï†úÌú¥ÏßÄÎèÑ */
.card3 { background-color: #3f72af; } /* FAQ */
.card4 { background-color: #5093df; } /* ÎØºÏõêÏ†ëÏàò */

.card1:hover,
.card2:hover,
.card3:hover,
.card4:hover {
  transform: scale(1.15);
}

.p {
  font-size: 5rem;
}

/* üì± Î™®Î∞îÏùº: 2 x 2 Grid Î†àÏù¥ÏïÑÏõÉ */
@media (max-width: 767px) {
  .card-section {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Îëê Í∞úÏùò Ïó¥ */
    grid-template-rows: repeat(2, 1fr);    /* Îëê Í∞úÏùò Ìñâ */
    width: 100%;
  }

  .card1, .card2, .card3, .card4 {
    width: 100%;   /* grid ÎÇ¥ÏóêÏÑúÎäî ÎÑàÎπÑ ÏûêÎèô */
    padding: 1rem;
  }
}

/* üßæ ÌÉúÎ∏îÎ¶ø: 2Í∞úÏî© 2Ïó¥ (flex Ïú†ÏßÄ Í∞ÄÎä•) */
@media (min-width: 768px) and (max-width: 1023px) {
  .card1, .card2, .card3, .card4 {
    width: 50%;
    padding: 2rem;
  }
}
